<template>
    <div class="container"><div class="comic_list"><div class="title"><h1>漫改作品</h1><span>全部</span>
    <span>少年</span>
    <span>少女</span><span>真人剧</span></div>
    <div class="window">
        <div class="pre" @click="state--" v-show="state > 1"></div>
        <ul :class="'loop' + state">
            <li v-for="(comic, index) in comics" :key=index>
                <a href="">
                    <img :src="'./img/漫改'+'/' +comic.name + '.jpg'" alt="">
                    <h2>{{comic.name}}</h2>
                    <p class="types">{{comic.types}}</p>
                </a>
            </li>
        </ul>
        <div class="next"  @click="state++" v-show="state < 2"></div>

    </div>
    </div>
    </div>
    
</template>

<script>
    import axios from "axios";
    export default {
        data(){
            return {
                comics:[],
                state:1,

            }
        },
        created(){
            var _this = this;
            axios.get("./img/漫改"+'/infor.json').then((response) => _this.comics = response.data.slice(0, 16)).catch((error)=>console.log(error))
        }
    }
</script>

