<template>
    <div class="hotlist"><div><h3>全站最热</h3><span @click="get_girl">少女</span>
    <span @click="get_boy">少年</span>
    <span @click="get_all">全部</span></div><hotitem v-for="(item,index) in used" :key=index :infor="item" :index="index+1"></hotitem>
    <div class="times"><span :class="{selected:curtimes == 1}" @click="toNthtimes(1)">1-10名</span>
    <span :class="{selected:curtimes == 2}" @click="toNthtimes(2)">11-20名</span>
    <span :class="{selected:curtimes == 3}" @click="toNthtimes(3)">21-30名</span></div>
    </div>
</template>

<script>
    import axios from "axios"
    import hotitem from './hotitem.vue'
    export default {
        components:{
            hotitem
        },
        props:{},
        created(){
            var _this = this;
            axios.get('hotData.json').then((response) =>{
                _this.total = response.data;
                _this.items = _this.total;
                _this.used = _this.items.slice(0,10);
            }).catch(function(error){
                console.log(error);
            });
        },
        data(){
            return {
                total: [],
                items:[],
                used:[],
                curtimes:1
            }
        },
        methods:{
            get_girl(){
                this.items = this.total.filter((val) => val.types.indexOf("少女") > -1)
                this.toNthtimes(this.curtimes);
            },
            get_boy(){
                this.items = this.total.filter((val) => val.types.indexOf("少年") > -1);
                this.toNthtimes(this.curtimes);
            },
            get_all(){
                this.items = this.total;
                this.toNthtimes(this.curtimes);
            },
            toNthtimes(n){
                this.curtimes = n;   
                this.used = this.items.slice(10 * (n-1), 10*n);
            }
        }
    }
</script>

<style scoped>
    div{
        color: white;
    }
    h3, span{
        display:inline-block;
        vertical-align: bottom;
        margin: 0;
    }
    .hotlist >div:first-child{
        margin-bottom: 1em;
    }
    .hotlist >div:first-child span{
        margin-left: 10px;
        float: right;
    }
    .hotlist{
        background: url(../assets/hot_bg.png);
        width: 227px;
        height: 400px;
        background-size: cover;
        padding: 1em;
        position: absolute;
        left: 8%;
        top: 50px;
        z-index: 50;
    }
    .times{
        display: flex;
        justify-content: space-between;
        position: absolute;
        bottom: 5px;
        width: 90%;
    }
    .items span{
        display: inline-block;

    }
    .selected{
        color: #d9616a;
    }
</style>