<template>
    <div class="container"><div class="comic_list"><div class="title"><h1>{{title}}</h1><span>全部</span>
    <span>少年</span>
    <span>少女</span><span class="look_more">更多<span class="look_more_icon"></span></span></div>
    <div class="window">
        <div class="pre" @click="state--" v-show="state > 1"></div>
        <ul :class="'loop' + state">
            <li v-for="(comic, index) in comics" :key=index>
                <a href="">
                    <img :src="'./img/'+title+'/' +comic.name + '.jpg'" alt="">
                    <h2>{{comic.name}}</h2>
                    <p class="types">{{comic.types}}</p>
                </a>
            </li>
        </ul>
        <div class="next"  @click="state++" v-show="state < 3"></div>

    </div>
    </div>
    <div class="up">
        <h2>上升最快</h2> <div class="comic_type"><span>全部</span><span>少年</span><span>少女</span></div>
        <ul><li v-for="(item,index) in increase" :key="index"><span class="times" :class="{color:index <3}">{{index+1}}</span><span>{{item.name}}</span><span class="tt">{{item.type}}</span></li></ul>
        <span class="see_more">查看更多 ></span>
    </div>
    </div>
    
</template>

<script>
    import axios from "axios";
    export default {
        props:["title"],
        data(){
            return {
                comics:[],
                state:1,
                increase:[{name:"镇魂街", type:"少年"}, {name:"空之恋人", type:"纯爱"},{name:"雏蜂", type:"少年"},{name:"当不良老大的男人", type:"纯爱"},{name:"锁龙", type:"少年"},{name:"超质体", type:"少年"},{name:"我的猫咪上仙", type:"纯爱"},{name:"噬规者", type:"少年"}]
            }
        },
        created(){
            var _this = this;
            axios.get("./img/"+this.title+'/infor.json').then((response) => _this.comics = response.data.slice(0,24)).catch((error)=>console.log(error))
        }
    }
</script>

<style scoped>
    .container{
        background-color: white;
        width: 1500px;
        height: 314px;
        font-family: "Microsoft YaHei", 黑体;
        position: relative;
        margin: 20px auto 0px;
        border-radius: 5px;
    }
    .comic_list{
        width: 1245px;
        float: left;
    }
    .title{
        position:relative;
    }
    .container h1{
        display: inline-block;
        color: #4a4a4a;
        margin: 0px;
    }
    .container span{
        display: inline-block;
        margin: 0 10px;
    }
    .container .look_more{
        position: absolute;
        right: 0px;
        bottom: 3px;
        line-height: 26px;
    }
    .container .look_more_icon{
        width: 26px;
        height: 26px;
        background: url(../assets/comiclist_ico.png) no-repeat -156px -519px;
        vertical-align: bottom;
    }
    .window{
        width: 100%;
        overflow: hidden;
        position: relative;

    }
    .window li{
        width: 137px;
        display: inline-block;
        margin-right: 18px;
    }
    .window ul{
        width: 3730px;
        padding-left: 18px;
        position: relative;
        transition: left 1s linear;
    }
    .window li img{
        width: 135px;
        height: 177px;
        display: block;
        border-radius: 10px;
        border: 1px solid #eeeded;
    }
    a{
        color: #4a4a4a;
        text-decoration: none;
        text-align: center;
    }
    a h2{
        font-size: 16px;

    }
    .window h2{
        white-space: nowrap;
        margin: 13px 0 10px;
    }
    .types{
        margin: 0px;
        font-size: 10px;
        color: #999
    }
    .window li img:hover{
        border: 1px solid #6dad14;
    }
    .pre{
        background: url(../assets/prevbtn_hover.png) no-repeat left center;
        width: 70px;
        height: 280px;
        position: absolute;
        left: 0;
        opacity: 0.8;
        cursor: pointer;
        z-index: 20;
    }
    .next{
        background: url(../assets/nextbtn_hover.png) no-repeat right center;
        width: 70px;
        height: 280px;
        position: absolute;
        opacity: 0.8;
        right: 0;
        top:0;
        cursor: pointer;
    }
    .pre:hover, .next:hover{
        opacity: 1;
    }
    .loop1{
        
        left: 0;
        
    }
    .loop2{
        left: -1240px;
    }
    .loop3{
        left: -2480px;
    }
    .up{
        width: 255px;
        height: 100%;
        float: left;
    }
    .up h2{
        float: left;
    }
    .up ul{
        list-style: none;
        clear: both;
        padding-left: 10px;
        padding-right: 10px;
    }
    .up li{
        margin: 5px 0;
    }
    .up .comic_type{
        float: right;
        margin-top: 25px;
        margin-right: 10px;

    }
    .up .times{
        display: block;
        float: left;
        width: 14px;
        height: 14px;
        line-height: 14px;
        text-align: center;
        background-color: #eee;
    }
    .up .color{
        background-color: #6dad14;
        color: white;
    }
    .up .tt{
        float:right;
        color: #999;
    }
    .up .see_more{
        color: #999;
        text-align: center;
        width: 100%;
        box-sizing: border-box;
        border: 1px solid #e0e0e0;
        border-radius: 10px;
        margin-top: 20px;
        padding: 5px 0;
    }
    .up .see_more:hover{
        background: #e0e0e0;
        color: #333;
    }
</style>