<template>
    <div class="containers" @mouseover="showTab=true" @mouseout="showTab = false"><span class="index" :class="{colorful:index<=3}">{{index}}</span> <div id="bl" v-if="!showTab"><span>{{infor.name}}</span> <span class="type">{{infor.mtype}}</span></div>
    <div id="tab" :class="{hidden: !showTab}">
        <img :src="baseurl + infor.name +'.jpg'" alt="">
        <div><h4>{{infor.name}}</h4>
        <span v-for="(type, index) in infor.types" :key=index>{{type}}</span>
        </div>
    </div>
    </div>
</template>

<script>
    export default {
        components:{},
        props:["infor","index"],
        data(){
            return {
                baseurl:"./img/",
                showTab:false,

            }
        },
       
        
    }
</script>

<style scoped>
    .containers{
        color: white;

    }
    .containers div{
        display: inline-block;
        vertical-align: top;
    }
    .containers > span{
        display: inline-block;
        width: 14px;
        height: 14px;
        line-height: 14px;
        text-align: center;
        margin-right: 10px;
    }
    .type{
        float:right;
    }
    .colorful{
        background-color: #9bd66d;
    }
    #bl{
        width: 88%;
    }
    img{
        width: 62px;
        height: 80px;
    }
    img + div{
        margin: 0 10px;
        width: 56%;
        display: inline-block;
    }
    h4{
        margin: 0px;
    }
    #tab span{
        display: inline-block;
        margin-top: 13px;
        width: 55px;
        height: 16px;
        line-height: 16px;
    }
    #tab span:before{
        content: '';
        display: inline-block;
        width: 3px;
        height: 3px;
        background:#999;
    }
    #tab{
        overflow: hidden;
        width: 88%;
    }
    .hidden{
        height: 0px;
    }
</style>